.CategoryHead {
  position: relative;
  display: block;
  max-width: 500px;
  width: 100%;
  padding: 12px;
  margin: 12px;
  height: 70px;

  .CategoryHead__category {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);

    & > span {
      display: inline-block;
      vertical-align: middle;
    }

    .CategoryHead__category-title {
      & > text {
        font-size: 26px;
        color: #282c34;
        font-weight: 700;
      }
    }

    .CategoryHead__category-icon {
      position: relative;
      width: 40px;
      height: 40px;
      padding: 12px;
      background-color: #79EA4F;
      border-radius: 1000px;
      margin-right: 12px;

      & > svg {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 35px;
        height: 35px;
        fill: #ffffff;
        transform: translate(-50%, -50%);
      }
    }
  }

  .CategoryHead__options {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);

    & > span {
      display: inline-block;
      margin: 4px;
      vertical-align: middle;
      
      & > button {
        border: none;
        outline: none;
        background: none;
        cursor: pointer;

        padding: 5px 12px;
        background-color: #ffffff;
        border-radius: 100px;
        box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
        transition: all .1s;

        &:hover {
          box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
        }

        & > span {
          display: inline-block;
          vertical-align: middle;

          &:nth-child(1) {
            margin-right: 5px;

            & > svg {
              fill: #5EC636;
              width: 18px;
              display: block;
            }
          }

          &:nth-child(2) {
            color: #323232;
          }
        }
      }
    }
  }
}