.NavTop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: 12px;
  background-color: #ffffff;
  box-shadow: 0px 1px 6px #757575;
  height: 40px;

  & > span {
    &:nth-child(1) {
      position: absolute;
      left: 12px;
      top: 50%;
      transform: translateY(-50%);

      & > span {
        display: inline-block;
        vertical-align: middle;

        &:nth-child(2) {
          & > img {
            width: 176px;
          }
        }

        &:nth-child(1) {
          margin-right: 12px;

          & > button {
            background: none;
            border: none;
            outline: none;
            cursor: pointer;
            height: 42px;
            width: 42px;
            border-radius: 1000px;

            &:hover {
              background-color: rgba(0, 0, 0, 0.05);
            }

            & > svg {
              fill: #323232;
            }
          }
        }
      }
    }

    &:nth-child(2) {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      padding: 4px;
      border-radius: 5px;
      transition: all .12s;
      background-color: #F1F3F4;

      &:focus-within {
        background-color: #ffffff;
        box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.33);
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;

        .NavTop__SearchAutocomplete {
            display: block;
        }
      }

      & > span {
        display: inline-block;
        vertical-align: middle;

        & > input {
          outline: none;
          background: none;
          border: none;
          width: 280px;

          height: 28px;
          padding-left: 8px;
          padding-right: 8px;
        }

        & > button {
          background: none;
          border: none;
          outline: none;
          cursor: pointer;
          height: 42px;
          width: 42px;
          border-radius: 1000px;

          &:hover {
            background-color: rgba(0, 0, 0, 0.05);
          }

          & > svg {
            fill: #323232;
          }
        }
      }
    }

    &:nth-child(3) {
      position: absolute;
      right: 12px;
      top: 50%;
      transform: translateY(-50%);

      & > span {
        display: inline-block;
        vertical-align: middle;

        & > button {
          border: none;
          outline: none;
          background: none;
          cursor: pointer;

          & > svg {
            display: block;
            fill: #5b5b5b;
          }
        }

        &:nth-child(1) {
          margin-right: 8px;
        }

        &:nth-child(2) {
          & > a {
            text-decoration: none;
            padding: 12px 18px;
            border-radius: 4px;
            font-size: 13px;
            font-weight: bold;
            font-family: sans-serif;
            color: #ffffff;
            background-color: #79EA4F;
            box-shadow: 0px 0px 15px inset rgba(0, 0, 0, 0.04);
            transition: all .12s;

            &:hover {
              background-color: #5EC636;
            }
          }
        }
      }
    }
  }
}

.NavTop__SearchAutocomplete {
  position: absolute;
  z-index: 1000;
  top: 50px;
  left: 0;
  background-color: #ffffff;
  width: calc(100% - 2px);
  padding-top: 12px;
  padding-bottom: 12px;
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.33);
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  display: none;
  margin: 0;
  border: 1px solid #DADCE0;

  & > ul {
    padding: 0;
    margin: 0;

    & > li {
      list-style: none;

      & > button {
        border: none;
        outline: none;
        background: none;
        cursor: pointer;
        width: 100%;
        text-align: left;

        padding: 12px 7px;

        &:hover {
          background-color: #eaecf0;
        }
      }
    }
  }
}