.ArticleList {
  padding: 0;
  margin: 0;
}

.ArticleList__Article {
  display: block;
  max-width: 500px;
  width: 100%;
  list-style: none;
  padding: 12px;
  border: 1px solid #DADCE0;
  border-radius: 5px;
  margin: 12px;
  text-align: left;

  & > span {
    display: inline-block;
    vertical-align: middle;

    &:nth-child(2) {
      width: 98px;
      height: 98px;
      overflow: hidden;
      position: relative;
      border-radius: 4px;
      
      & > img {
        transform: translateX(-50%);
        position: absolute;
        left: 50%;
        height: 100%;
      }
    }

    &:nth-child(1) {
      width: 76%;
      margin-right: 12px;

      & > a {
        text-decoration: none;
        color: #050505;

        &:hover {
          text-decoration: underline;
        }

        & > h3 {
          margin: 0;
          font-size: 18px;
        }

        & > div {
          & > span {
            display: inline-block;
            vertical-align: middle;
            color: #787878;
            font-size: 12px;

            &:nth-child(2) {
              margin-right: 4px;
              margin-left: 4px;
              font-size: 8px;
            }
          }
        }
      }
    }
  }
}