.Weather {
  padding: 14px;
  border-radius: 5px;
  border: 1px solid #DADCE0;
  max-width: 380px;
  width: 100%;
  margin: 16px;

  & > strong {
    text-align: left;
    display: block;
    font-size: 15px;
    font-weight: bold;
    color: #545454;
    font-family: sans-serif;
  }

  & > hr {
    border: none;
    border-bottom: 1px solid #DADCE0;
  }

  .Weather__current {
    margin-bottom: 9px;
    margin-top: 9px;
    text-align: left;
    height: 80px;
    position: relative;
    
    & > span {
      text-align: left;

      &:nth-child(1) {
        position: absolute;
        left: 9px;
        top: 50%;
        transform: translateY(-50%);

        & > text {
          &:nth-child(1) {
            font-family: sans-serif;
            font-size: 13px;
            color: #545454;
            font-weight: bold;
          }

          &:nth-child(3) {
            font-size: 28px;
            color: #323232;
          }
        }
      }

      &:nth-child(2) {
        position: absolute;
        right: 9px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  .Weather__tomorrow {
    & > span {
      display: inline-block;
      margin: 5px;

      & > text {
        &:nth-child(1) {
          font-family: sans-serif;
          font-size: 13px;
          color: #323232;
          font-weight: bold;
        }

        &:nth-child(5) {
          font-size: 12px;
          color: #323232;
          font-weight: bold;
        }

        &:nth-child(7) {
          font-size: 12px;
          color: #606060;
        }
      }
    }
  }

  .Weather__bottom {
    position: relative;
    height: 20px;
    & > span {
      &:nth-child(1) {
        position: absolute;
        left: 9px;
        top: 50%;
        transform: translateY(-50%);

        & > ul {
          padding: 0;
          margin: 0;

          & > li {
            list-style: none;
            display: inline-block;
            vertical-align: middle;


            &:not(:last-child)::after {
              content: "|";
              font-size: 12px;
              color: #282c34;
            }
            
            & > button {
              background: none;
              border: none;
              outline: none;
              cursor: pointer;
              font-size: 13px;
              color: #323232;
              transition: all .12s;

              &:hover {
                color: #5EC636;
              }
            }
          }
        }
      }

      &:nth-child(2) {
        position: absolute;
        right: 9px;
        top: 50%;
        transform: translateY(-50%);

        & > a {
          text-decoration: none;
          font-size: 13px;
          color: #5EC636;
        }
      }
    }
  }
}