.TerrorCounter {
  padding: 14px;
  border-radius: 5px;
  border: 1px solid #DADCE0;
  margin: 16px;
  max-width: 380px;
  width: 100%;

  & > strong {
    text-align: left;
    display: block;
    font-size: 15px;
    font-weight: bold;
    color: #545454;
    font-family: sans-serif;
  }

  & > hr {
    border: none;
    border-bottom: 1px solid #DADCE0;
  }

  .TerrorCounter__count {
    & > table {
      & > tr {
        text-align: left;

        & > th {
          font-family: sans-serif;
          font-size: 13px;
          color: #545454;
          font-weight: bold;
          padding: 4px;
          padding-left: 5px;
        }

        & > td {
          padding: 4px;
          padding-left: 5px;
          color: #545454;
          font-size: 13px;
        }
      }
    }
  }

  .TerrorCounter__recent {
    margin-bottom: 9px;
    margin-top: 9px;
    text-align: left;
    height: 80px;
    position: relative;
    
    & > span {
      text-align: left;

      &:nth-child(1) {
        position: absolute;
        left: 9px;
        top: 50%;
        transform: translateY(-50%);

        & > text {
          &:nth-child(1) {
            font-family: sans-serif;
            font-size: 13px;
            color: #545454;
            font-weight: bold;
          }

          &:nth-child(3) {
            font-size: 28px;
            color: #323232;
          }
        }
      }

      &:nth-child(2) {
        position: absolute;
        right: 9px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  .TerrorCounter__bottom {
    text-align: right;

    & > span {
      text-decoration: none;
      font-size: 13px;
      color: #5EC636;
    }
  }
}