.NavSideList {
  & > ul {
    padding: 0;
    margin: 0;
    text-align: left;

    & > li {
      list-style: none;

      & > a {
        width: 220px;
        display: block;
        padding: 9px;
        padding-right: 16px;
        text-decoration: none;
        border-top-right-radius: 100px;
        border-bottom-right-radius: 100px;
        font-size: 13px;
        font-family: sans-serif;

        &:hover {
          color: #5EC636;
          fill: #5EC636;
        }

        & > span {
          display: inline-block;
          vertical-align: middle;

          &:nth-child(1) {
            margin-right: 8px;

            & > svg {
              display: block;
            }
          }
        }
      }
    }
  }
}

.NavSideList__Item-Active {
  background-color: rgba(121, 234, 79, 0.2);

  & > span {
    &:nth-child(2) {
      color: #5EC636;
    }

    &:nth-child(1) {
      & > svg {
        fill: #5EC636;
      }
    }
  }
}

.NavSideList__Item-InActive {
  color: #3f3f3f;
  fill: #5b5b5b;
}