.BottomNotification {
  position: fixed;
  bottom: 12px;
  left: 12px;
  padding: 10px;
  color: #ffffff;
  min-width: 150px;
  font-size: 14px;
  background-color: #323232;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.41);
  border-radius: 3px;
}

.BottomNotification__active {
  transition: all .2s;
  animation: BottomNotificationIn .3s both;
}

.BottomNotification__inactive {
  animation: BottomNotificationOut .2s both;
}

@keyframes BottomNotificationOut {
  0% {
    transform: translateY(0px);
  }

  100% {
    transform: translateY(200px);
  }
}

@keyframes BottomNotificationIn {
  0% {
    transform: translateY(200px);
  }

  100% {
    transform: translateY(0px);
  }
}